.clear {clear:both;}
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {background: transparent;border: 0;margin: 0;padding: 0;vertical-align: baseline;}
body { color: #777777;  font-family: "Helvetica Neue", Helvetica, "Open Sans", Arial, sans-serif;  font-size: 18px;  line-height: 26px; font-weight:300;}
p, dl, hr, h1, h4, h5, h6, ol, ul, pre, table, address, fieldset, figure {font-weight: 300;  margin-bottom: 20px;  line-height: 28px;}
h1, h2, h3, h4, h5, h6{ color: #555555; font-weight: 600; letter-spacing: -0.5px;}
h2 {margin-top: 40px; margin-bottom: 20px;}
h3 {margin-top: 30px; margin-bottom: 20px;}
pre {  overflow: auto;  width: 98%;}
embed {max-width: 100%;}
ol{list-style: decimal outside none;}
ul{list-style-type: disc;}
li{margin-left: 30px;}
dl {margin: 0 0 24px;float: left;}
dt {font-weight: 400;}
dd {margin-bottom: 18px;}
cite{font-weight: 400;}
blockquote cite a{color: #FFFFFF;}
iframe { max-width: 100%; width:100%;outline: none;border:none;overflow:hidden;border:none;}
blockquote:before, blockquote:after {  content: '';content: none;}
del {text-decoration: line-through;}

/* tables still need 'cellspacing="0"' in the markup */
table {border-collapse: collapse;  border-spacing: 0;  }
th{color:#000; text-align: center;font-weight: 400;  }
th,td{padding:3px 5px;  border: 1px solid #CCCCCC; }
a img {border: none;}
a {text-decoration: none;color: #FFA500;}
a, img, a img, input[type="text"], input[type="password"], input, textarea {outline:none;}
a:hover{ color: #555; }


