 a.skt-featured-icons:before {border-bottom: 25px solid #FFA500; border-left: 44px solid rgba(0, 0, 0, 0); border-right: 44px solid rgba(0, 0, 0, 0); content: ""; height: 0; left: 0; position: absolute; top: -25px; width: 0; }
