
#front-content-box .container
{ padding-top: 45px;}

#front-content-box.block--about .container {
padding-top:0;}

#front-content-box.block--blog .container, .category-blog .bread-title-holder .container
{ @include wash-line($color-blog) }

#front-content-box.block--features .container, .category-features .bread-title-holder .container
{ @include wash-line($color-features) }

.page-id-8 .bread-title-holder .container, .category-about .bread-title-holder .container
{ @include wash-line($color-about) }

#front-content-box.block--training .container, .category-training .bread-title-holder .container
{ @include wash-line($color-training) }

/*.tribe-theme-edpsy #skehead > .container
{border-bottom-width: 3px; border-bottom-style: solid; border-color: $color-events}
*/

.tribe-theme-edpsy .bread-title-holder .container
{ @include wash-line($color-events) }

.tribe-theme-edpsy .skehead-headernav-shrink #skehead > .container
{border-bottom: none;}

.blog-template #content, .blog #content {margin-bottom:17px;}

.tag #content, .category #content,.archive  #content, .search  #content {margin-bottom:15px;}

.blog-template .container.post-wrap, .blog .container.post-wrap {margin-top:60px;}

.blog-template .navigation #advertica-paginate, .blog .navigation #advertica-paginate {margin-bottom:38px;}

.block--blog h2 a, .block--features h2 a, .block--training h2 a {
	  border-bottom-width: 2px;
	  border-bottom-style: solid;
  }

.block--blog h2 a:link, .block--blog h2 a:visited, .block--features h2 a:link, .block--features h2 a:visited, .block--training h2 a:link, .block--training h2 a:visited {
	  border-bottom-color: #fff;
  }

.block--blog h2 a:hover, .block--blog h2 a:active {
   	  border-bottom-color: $color-blog;
}

.block--features h2 a:hover, .block--features h2 a:active {
   	  border-bottom-color: $color-features;
}

.block--training h2 a:hover, .block--training h2 a:active {
   	  border-bottom-color: $color-training;
}





/*   */
a:link p, a:visited p { color:$color-copy ;}

.post #content .skepost p:first-child {
font-size: 1.5em;
line-height: 1.3em;
}


/* Blocks */

#front-content-blocks .container {border-top: $color-features 4px solid;}

#front-blog-wrap .span4 {padding-bottom:25px;}

#front-blog-wrap .span4 a:link, #front-blog-wrap .span4 a:visited {background-color:$color-blog;}

#front-blog-wrap .span4 a:hover, #front-blog-wrap .span4 a:active {background-color:$color-blog-highlight;}

#front-blog-wrap .span4.feature-posts a:link, #front-blog-wrap .span4.feature-posts a:visited {background-color:$color-features;}

#front-blog-wrap .span4.feature-posts a:hover, #front-blog-wrap .span4.feature-posts a:active {background-color:$color-features-highlight;}

#front-blog-wrap .span3.training-posts a:link, #front-blog-wrap .span3.training-posts a:visited {background-color:$color-training;}

#front-blog-wrap .span3.training-posts a:hover, #front-blog-wrap .span3.training-posts a:active {background-color:$color-training-highlight;}

#front-blog-wrap .span4 a:hover, #front-blog-wrap .span4 a:active, #front-blog-wrap .span3 a:hover, #front-blog-wrap .span3 a:active {display: block; box-shadow: 0px 0px 12px #8C8A8A;}

.block--blog .blog-posts a, .block--features .feature-posts a, .block--training .training-posts a  {padding: 0px; display: block;}

.size-edpsy_loop_img {width:100%; height:auto;}

.third_wrapper .foot-message {font-size: 1.4rem; line-height: 2.2rem; padding-top: 15px;}

.block--features {margin-top:0!important; }
.block--training { margin-top:0!important;}
.block--promo {margin-top: -10px; margin-bottom: 70px;}

.bg--dark h2, .bg--dark a, .bg--dark a:link, .bg--dark a:visited {color:$color-copy-reverse}

.skt-section.block--about {margin-top:30px; margin-bottom:40px;}

p.front-section-extra { margin-bottom: 45px; text-align: right; clear:left;}
.front-section-extra a {text-decoration: underline;}
.skt-section a:hover {
    transition: all 0.1s linear 0s;
    -webkit-transition: all 0.1s linear 0s;
    -moz-transition: all 0.1s linear 0s;
    -o-transition: all 0.1s linear 0s;}

#front-content-box h3 {margin-top: 0;}

#featured-box{margin-top:95px;}

.feature-posts .quote {font-size: 2.5rem;
line-height: 2.8rem;
padding-top: 60px;
padding-left: 20px;
padding-right: 60px;
}

